import React from 'react';

import {Button, Checkbox, Container, Grid, Link, Typography} from "@mui/material";

const MyAccount = () => {
    return (
        <Container maxWidth={"lg"}>
            <Grid container xs={12}>
                <Grid  item sm={6} xs={12} spacing={8} className={"p-3"} >
                    <h3 className={"mb-3"}></h3>
                    <Typography>*     </Typography>
                    <input className={"border-0  rounded bg-light p-3 w-100"}/>
                    <Typography>* </Typography>
                    <input className={"border-0 rounded bg-light p-3 w-100"}/>
                    <div>
                        <Checkbox />
                        <Typography className={"d-inline"}>   </Typography>
                    </div>
                    <Button color={"primary"} variant={"contained"} className={"my-3"}></Button>
                    <Link href={"#"} className={" d-block"}>     </Link>
                </Grid>
                <Grid  item sm={6} xs={12} spacing={8} className={"p-3"}>
                    <h3 className={"mb-3"}></h3>
                    <Typography>*  </Typography>
                    <input className={"border-0 rounded bg-light p-3 w-100"}/>
                    <Typography>* </Typography>
                    <input className={"border-0 rounded bg-light p-3 w-100"}/>
                    <Button color={"primary"} variant={"contained"} className={"my-3"}></Button>
                </Grid>
            </Grid>
        </Container>
    );
};

export default MyAccount;