import React from 'react';
import {Button, Typography} from "@material-ui/core";

const OrderTracking = () => {
    return (
        <form className={" d-flex row align-content-around p-5 m-0"} style={{height:"600px"}}>
            <Typography  style={{fontSize:"x-large"}}>                     .                 .</Typography>
            <label>  (ID):</label>
            <input className={"bg-light border-0 py-4 rounded"} placeholder={"         "} name={"number"}/>
            <label> :</label>
            <input className={"bg-light border-0 py-4 rounded"} placeholder={"       "} name={"email"}/>
            <div className={"d-flex justify-content-end"}>
                <Button className={" px-4 py-3"} variant={"contained"} color={"primary"} type={"submit"}></Button>
            </div>
        </form>
    );
};

export default OrderTracking;